import config from 'config';
import dateFormat from 'dateformat';

export default class DateUtil {

    /**
     * get current date time
     * @returns String
     */
    static getCurrentDateTime() {
        return dateFormat(new Date(),config.DEFAULT_DATETIME_SUBMIT_FORMATER);
    }

    /**
     * Convert js Date to string
     * @param date - Date
     * @returns String
     */
    static formatToSubmitFormater(date) {
        if (date == undefined || date == null) {
            return null;
        } else {
            return dateFormat(date,config.DEFAULT_DATETIME_SUBMIT_FORMATER);
        }
    }

    /**
     * Convert str to Moment
     * @param strDate - String
     * @returns moment
     */
    static toMoment(strDate) {
        if (strDate == undefined || strDate == null) {
            return null;
        } else {
            return dateFormat(strDate, config.DEFAULT_DATETIME_SUBMIT_FORMATER);
        }
    }


    static getDateFormat() {
        return dateFormat;
    }

}




